import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CommunityMainComponent } from './components/community-main/community-main.component';
import { CommunityLoginComponent } from './components/community-login/community-login.component';
import { CommunityAuthGuard } from './guards/community-auth.guard';

const routes: Routes = [
  {
    path: 'login',
    component: CommunityLoginComponent
  },
  {
    path: '',
    component: CommunityMainComponent,
    canActivate: [CommunityAuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CommunityRoutingModule { }
