import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { CommunityAuthService } from '../services/community-auth.service';

@Injectable({
  providedIn: 'root'
})
export class CommunityAuthGuard implements CanActivate {
  constructor(
    private authService: CommunityAuthService,
    private router: Router
  ) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    const isAuth = this.authService.isAuthenticated();
    console.log('[CommunityGuard] Ïù∏Ï¶ù ÌôïÏù∏:', isAuth, '| ÏöîÏ≤≠ URL:', state.url);
    
    if (isAuth) {
      return true;
    }

    console.log('[CommunityGuard] ÎØ∏Ïù∏Ï¶ù ÏÇ¨Ïö©Ïûê, Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô');
    this.router.navigate(['/community/login'], {
      queryParams: { returnUrl: state.url }
    });
    return false;
  }
}
