import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, of, BehaviorSubject } from 'rxjs';

interface CommunityUser {
  userId: string;
  username: string;
  email: string;
  nickname: string;
  avatar?: string;
  level: number;
  points: number;
  posts: number;
  token: string;
}

@Injectable({
  providedIn: 'root'
})
export class CommunityAuthService {
  private readonly STORAGE_KEY = 'community_user';
  private readonly TOKEN_KEY = 'community_auth_token';
  
  // ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê ÏÉÅÌÉúÎ•º Í¥ÄÏ∞∞ Í∞ÄÎä•ÌïòÍ≤å ÎßåÎì¶
  private currentUserSubject = new BehaviorSubject<CommunityUser | null>(null);
  public currentUser$ = this.currentUserSubject.asObservable();

  constructor(private router: Router) {
    // Ï¥àÍ∏∞Ìôî Ïãú Ï†ÄÏû•Îêú ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Î°úÎìú
    this.loadUserFromStorage();
  }
  
  private loadUserFromStorage(): void {
    const user = this.getCurrentUser();
    this.currentUserSubject.next(user);
  }

  login(username: string, password: string): Observable<boolean> {
    console.log('[CommunityAuth] Î°úÍ∑∏Ïù∏ ÏãúÎèÑ:', username);
    return this.mockLogin(username, password);
  }

  private mockLogin(username: string, password: string): Observable<boolean> {
    const mockUsers: { [key: string]: CommunityUser } = {
      'admin': {
        userId: 'comm_admin',
        username: 'admin',
        email: 'admin@community.com',
        nickname: 'Í¥ÄÎ¶¨Ïûê',
        avatar: 'üëë',
        level: 99,
        points: 99999,
        posts: 500,
        token: 'mock_community_admin'
      },
      'user1': {
        userId: 'comm_user1',
        username: 'user1',
        email: 'user1@community.com',
        nickname: 'ÌôúÎèôÍ∞Ä',
        avatar: 'üåü',
        level: 15,
        points: 3500,
        posts: 87,
        token: 'mock_community_user1'
      }
    };

    if (mockUsers[username] && password === '1017') {
      const user = mockUsers[username];
      this.setUser(user);
      this.setToken(user.token);
      this.currentUserSubject.next(user); // ÏÇ¨Ïö©Ïûê ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏
      console.log('[CommunityAuth] Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ:', user.nickname);
      return of(true);
    }

    console.log('[CommunityAuth] Î°úÍ∑∏Ïù∏ Ïã§Ìå®');
    return of(false);
  }

  logout(): void {
    console.log('[CommunityAuth] Î°úÍ∑∏ÏïÑÏõÉ ÏãúÏûë');
    
    try {
      // localStorage ÌÅ¥Î¶¨Ïñ¥
      localStorage.removeItem(this.STORAGE_KEY);
      localStorage.removeItem(this.TOKEN_KEY);
      
      // ÏÇ¨Ïö©Ïûê ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî
      this.currentUserSubject.next(null);
      
      console.log('[CommunityAuth] Î°úÍ∑∏ÏïÑÏõÉ ÏôÑÎ£å, Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô');
    } catch (error) {
      console.error('[CommunityAuth] Î°úÍ∑∏ÏïÑÏõÉ Ï§ë Ïò§Î•ò:', error);
    } finally {
      // Ïò§Î•ò Î∞úÏÉù Ïó¨Î∂ÄÏôÄ Í¥ÄÍ≥ÑÏóÜÏù¥ Ìï≠ÏÉÅ Ïª§ÎÆ§ÎãàÌã∞ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
      this.router.navigate(['/community/login']).then(success => {
        if (success) {
          console.log('[CommunityAuth] Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ÏÑ±Í≥µ');
        } else {
          console.error('[CommunityAuth] Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ïã§Ìå®, Í∞ïÏ†ú Ïù¥Îèô ÏãúÎèÑ');
          // ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ïã§Ìå® ÏãúÏóêÎèÑ Í∞ïÏ†úÎ°ú URL Î≥ÄÍ≤Ω
          window.location.hash = '#/community/login';
        }
      }).catch(err => {
        console.error('[CommunityAuth] ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ÏòàÏô∏ Î∞úÏÉù:', err);
        // ÏòàÏô∏ Î∞úÏÉù ÏãúÏóêÎèÑ Í∞ïÏ†úÎ°ú URL Î≥ÄÍ≤Ω
        window.location.hash = '#/community/login';
      });
    }
  }

  isAuthenticated(): boolean {
    const token = localStorage.getItem(this.TOKEN_KEY);
    return token !== null && token.trim() !== '';
  }

  getCurrentUser(): CommunityUser | null {
    const userStr = localStorage.getItem(this.STORAGE_KEY);
    if (userStr) {
      try {
        return JSON.parse(userStr);
      } catch {
        return null;
      }
    }
    return null;
  }

  private setUser(user: CommunityUser): void {
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(user));
  }

  private setToken(token: string): void {
    localStorage.setItem(this.TOKEN_KEY, token);
  }
}
